<template>
    <div id="section1">
        <div id="bg"></div>
        <div id="menu-container"></div>
        <div class='circle-container'>
            <router-link to="/" exact class='center'><div><img class="menu-image" src="../../assets/images/Home.png"/><br/>Home</div></router-link>
            <router-link to='about' class='deg0'><div><img class="menu-image" src="../../assets/images/About.png"/><br/>About</div></router-link>
            <router-link to='approach' class='deg45'><div><img class="menu-image" src="../../assets/images/Approach.png"/><br/>Approach</div></router-link>
            <router-link to='team' class='deg135'><div><img class="menu-image" src="../../assets/images/Team.png"/><br/>Team</div></router-link>
            <router-link to='invetment' class='deg180'><div><img class="menu-image" src="../../assets/images/Investments.png"/><br/>Investments</div></router-link>
            <router-link to='news' class='deg225'><div><img class="menu-image" src="../../assets/images/News.png"/><br/>News</div></router-link>
            <router-link to='contact' class='deg315'><div><img class="menu-image" src="../../assets/images/Contact_Us.png"/><br/>Contact Us</div></router-link>
        </div>
    </div>
</template>

<script>
export default {
  name: 'section1'
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    #section1{
        width:100%;
        height:100%;
        position:relative;
    }
    #bg{
        width:100%;
        height:100%;
        background: black;
        opacity: 0.4;
    }
    #menu-container{
        position:absolute;
        width: 48vw;
        height: 48vw;
        /* height: 70%; */
        /* top:13%; */
        top: 50%;
        transform: translateY(-47%);
        left:0;
        right:0;
        background: white;
        border-radius:50%;
        margin:0px auto;
        opacity: 0.8;
    }
    .circle-container {
        position:absolute;
        /* top:47vw; */
        top: 45%;
        transform: translateY(-45%);
        left:0;
        right:0;
        margin:0px auto;
    }
    .circle-container a {
        display: block;
        overflow: hidden;
        position: absolute;
        top: 50%;
        left:0;
        right:0;
        margin: 0px auto;
        text-decoration: none;
        width: 8%; height: auto;
        font-size: .8em;
    }
    .menu-image {
        width:100%;
    }
    .deg0 { transform: translate(200%); } /* 12em = half the width of the wrapper */
    .deg45 { transform: rotate(55deg) translate(200%) rotate(-55deg); }
    .deg135 { transform: rotate(125deg) translate(200%) rotate(-125deg); }
    .deg180 { transform: translate(-200%); }
    .deg225 { transform: rotate(235deg) translate(200%) rotate(-235deg); }
    .deg315 { transform: rotate(305deg) translate(200%) rotate(-305deg); }

    /* this is just for showing the angle on hover */
    .circle-container a:not(.center):before {
        position: absolute;
        width: 4em;
        color: white;
        opacity: 0;
        background: rgba(0,0,0,.5);
        font: 1.25em/3.45 Courier, monospace;
        letter-spacing: 2px;
        text-decoration: none;
        text-indent: -2em;
        text-shadow: 0 0 .1em deeppink;
        transition: .7s; /* only works in Firefox */
        content: attr(class)'°';
    }
</style>



